'use client'
import { Archivo_Black } from 'next/font/google'
import React, { useEffect, useState } from 'react'
import ReactRain from 'react-rain-animation'
import { TypeAnimation } from 'react-type-animation'
import CopyrightBanner from 'src/components/copyrightBanner.component'
import LogotypeBanner from 'src/components/logotypeBanner.component'
import { Button } from 'src/components/ui/button'
import { Progress } from 'src/components/ui/progress'
import useMousePosition from 'src/lib/useMousePosition'
import { cn } from 'src/lib/utils'

const font = Archivo_Black({
	variable: '--font-sans',
	weight: '400',
})

const commandlineEntryTempl = [
	'Server started on port 3000',
	'Database connected successfully',
	'GET\trequest received at /api/users',
	'POST\trequest received at /api/users',
	'User authenticated successfully',
	'Error\tUnable to connect to database',
	'Warning\tHigh memory usage detected',
	'Info\tServer uptime is 24 hours',
]

const HomePage: React.FC<Readonly<{}>> = () => {
	const [commandlineEntries, setCommandlineEntries] = useState<React.ReactNode[]>([])
	const [progressLine, setProgressLine] = useState<number>(0)
	const mousePosition = useMousePosition()

	const addCommandlineEntry = () => {
		setCommandlineEntries(prevState => {
			const commandlineEntry = commandlineEntryTempl[Math.floor(Math.random() * commandlineEntryTempl.length)]
			let newState = [...prevState]

			const getSequenceOrSelf = entry => (entry.props ? entry.props.sequence[0] ?? entry : entry)

			if (newState.length >= 3) {
				newState = newState.map(getSequenceOrSelf).slice(1)
			}

			if (progressLine < 100) {
				setProgressLine(prevState => {
					const increment = Math.floor(Math.random() * 10) + 1
					return Math.min(prevState + increment, 100)
				})
			}

			newState.push(<TypeAnimation key={commandlineEntry} cursor={false} sequence={[commandlineEntry]} />)
			return newState
		})
	}

	const transformStyle = (zIndex = 0, scale = 1) =>
		`rotateX(${(mousePosition.y ?? 0 - window.innerWidth / 2) * -0.005}deg) rotateY(${(mousePosition.x ?? 0 - window.innerHeight / 2) * -0.01}deg) translateZ(${zIndex}rem) scale(${scale})`

	useEffect(() => {
		const interval = setInterval(addCommandlineEntry, 2000)
		return () => clearInterval(interval)
	}, [])

	return (
		<>
			<header className={cn(`w-full h-[5vh]`)}></header>
			<main className="perspective-1000 overflow-hidden h-[90vh]">
				<section className={cn(`h-full transform-style-3d will-change-transform transition-transform `)}>
					<div
						className={cn(`absolute -inset-52 bg-cover bg-center bg-city-wallpaper`)}
						style={{ transform: transformStyle(0) }}
					></div>
					<div className={cn(`absolute`)} style={{ transform: transformStyle(5) }}>
						<ReactRain numDrops="20" />
					</div>
					<div
						className="absolute inset-0 bg-cover bg-center rotate-y-2 flex items-center justify-center"
						style={{ transform: transformStyle(10) }}
					>
						<LogotypeBanner />
					</div>
					<div
						className="absolute inset-0 rotate-y-2 flex flex-col items-center justify-center"
						style={{ transform: transformStyle(5) }}
					>
						<div className="flex items-start justify-between max-w-md w-full gap-4 mt-64">
							<span className="hidden sm:block">workspace</span>
							<ul className="max-w-96 marker:text-constructive list-disc list-inside bg-card/50 backdrop-blur-sm rounded-b-md w-full mt-0.5 px-2 divide-y-2">
								{commandlineEntries.map((commandlineEntry, index) => (
									<li key={index}>{commandlineEntry}</li>
								))}
							</ul>
						</div>
						<div className="h-20"></div>
						<Progress value={progressLine} className="max-w-md" />
					</div>
					<div className="absolute bottom-52 w-full bg-center rotate-y-2 flex items-center justify-center gap-4 translate-z-[15rem]">
						<Button
							className={progressLine != 100 ? `hover:bg-secondary` : ``}
							variant={progressLine != 100 ? `secondary` : `default`}
						>
							Dashboard
						</Button>
					</div>
				</section>
			</main>
			<footer className="w-full h-[5vh] flex justify-center items-center">
				<CopyrightBanner />
			</footer>
			<div className="h-[100vh]"></div>
		</>
	)
}

export default HomePage
